import {
  Box,
  Card,
  CardContent,
  Grid,
  Typography,
  useMediaQuery,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { useTheme } from "@mui/material/styles";

const CardProject = ({ data }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  const CardProjectDesktop = () => {
    return (
      <React.Fragment>
        <Grid container columns={20} alignItems={"center"} height={"auto"}>
          <Grid item xs={2}></Grid>

          <Grid item xs={16}>
            {/* Start Card Project */}
            {data.map((item, index) => (
              <div key={index}>
                <Card sx={{ margin: "4%" }}>
                  <CardContent height={"100%"}>
                    <Grid
                      container
                      columns={20}
                      alignItems={"center"}
                      justifyItems={"center"}
                    >
                      <Grid item xs align="center">
                        <Box>
                          <img
                            height={"225px"}
                            alt={item.title}
                            src={require(`../assets/images/${item.src_image}`)}
                            style={{
                              borderRadius: "3%",
                            }}
                          />
                        </Box>
                      </Grid>

                      <Grid item xs>
                        <Typography variant="h4" align="center">
                          {item.title}
                        </Typography>
                        <Typography variant="body1" sx={{ mt: "5px" }}>
                          {item.description}
                        </Typography>
                      </Grid>
                    </Grid>
                  </CardContent>
                </Card>
              </div>
            ))}
            {/* End Card Project */}
          </Grid>
          <Grid item xs={2}></Grid>
        </Grid>
      </React.Fragment>
    );
  };

  const CardProjectMobile = () => {
    return (
      <React.Fragment>
        <Grid
          container
          columns={20}
          alignItems={"center"}
          height={"auto"}
          width={"100%"}
        >
          <Grid item xs={1}></Grid>

          <Grid item xs={18}>
            {/* Start Card Project */}
            {data.map((item, index) => (
              <div>
                <Card key={index} sx={{ width: "auto", margin: "2%" }}>
                  <CardContent height={"100%"}>
                    <Grid container direction={"column"} alignItems={"strech"}>
                      <Grid item xs align="center">
                        <img
                          width={"100%"}
                          alt={item.title}
                          src={require(`../assets/images/${item.src_image}`)}
                          style={{ borderRadius: "3%" }}
                        />
                      </Grid>

                      <Grid item xs mt={"2rem"}>
                        <Typography variant="h4" align="center">
                          {item.title}
                        </Typography>
                        <Typography variant="body1" sx={{ mt: "1.5rem" }}>
                          {item.description}
                        </Typography>
                      </Grid>
                    </Grid>
                  </CardContent>
                </Card>
              </div>
            ))}
            {/* End Card Project */}
          </Grid>
          <Grid item xs={1}></Grid>
        </Grid>
      </React.Fragment>
    );
  };

  if (isMobile) {
    return (
      <React.Fragment>
        <CardProjectMobile />
      </React.Fragment>
    );
  } else {
    return (
      <React.Fragment>
        <CardProjectDesktop />
      </React.Fragment>
    );
  }
};

export default CardProject;
